<template>
    <div class="login">
        <div class="loginwidth">
            <h1>登录</h1>
            <hr>
            <div class="login_content">
                <div class="c_left">

                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
                        <el-form-item prop="adminName" label="账号">
                            <el-input placeholder="请输入您尊贵的账号^_^" type="text" v-model="ruleForm.adminName">
                            </el-input>
                        </el-form-item>

                        <el-form-item prop="passWord" label="密码">
                            <el-input placeholder="请输入您珍藏的密码^_^" type="password" v-model="ruleForm.passWord" @keyup.enter.native="submitForm('ruleForm')">
                            </el-input>
                        </el-form-item>

                        <el-form-item>
                            <el-button type="primary" round @click="submitForm('ruleForm')" :loading="loading">点击登录</el-button>

                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" round @click="resetForm('ruleForm')">重置</el-button>
                        </el-form-item>

                    </el-form>

                </div>

                <div class="c_center">

                </div>

                <div class="c_right">
                    <h3>没有账号怎么办?</h3>
                    <p>拥有账号后您将拥有以下权利</p>
                    <ul>
                        <li>新用户优惠券</li>
                        <li>个性化推荐内容</li>
                        <li>订单跟踪</li>
                    </ul>
                    <el-button type="primary" round @click="">创建</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import 'element-ui/lib/theme-chalk/index.css';
    export default {
        name: "login-content",
        data() {
            return {
                loading:false,
                ruleForm: {
                    adminName: '',
                    passWord: ''
                },
                rules: {
                    adminName: [
                        {required: true, message: '请您输入账号', trigger: 'blur'},
                        {min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur'}
                    ],
                    passWord: [
                        {required: true, message: '请您输入密码', trigger: 'blur'},
                        {min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            submitForm(formName) {

                //对表单进行验证，并放到回调函数中
                this.$refs[formName].validate((valid) => {

                    console.log(valid);

                    if (valid) {//输入的内容符合要求

                        this.loading=true;

                        this.$store.dispatch("adminLogin",{
                            data:{
                                adminName:this.ruleForm.adminName,
                                passWord:this.ruleForm.passWord
                            },
                            success:()=>{
                                console.log(this)
                                this.loading = false;
                            }

                        })
                        // alert('submit!');
                    } else {//输入的内容不符合要求
                        // console.log('error submit!!');
                        // return false;
                        this.$message({
                            showClose: true,
                            message: '请您输入正确的验证信息',
                            type: 'error'
                        });
                    }
                });
            },

            //重置按钮
            resetForm(formName) {
                this.loading=false;
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style lang="less">
    .login{
        background: #E7E7E7;
        overflow: hidden;
        padding: 60px 0 100px 0;
        .loginwidth{
            width: 901px;
            margin: 0 auto;
            h1{
                text-align: center;
                font-size: 36px;
            }
            hr{
                background: #D4CEC9;
                margin-bottom: 15px;
            }
            .login_content{
                .c_left{
                    width:450px;
                    float: left;
                    padding: 10px;
                    padding-top: 20px;
                    .el-button{
                        width: 100%;
                        background: #1B1B1B;
                        border: none;
                        /*margin: 10px auto;*/



                    }
                    .el-button.is-round{
                        border-radius: 5px;
                    }


                }
                .c_center{
                    width: 1px;
                    min-height: 380px;
                    background: white;
                    float: left;
                }
                .c_right{
                    width: 450px;
                    float: left;
                    padding: 10px;
                    padding-top: 20px;
                    font-size: 14px;
                    h3{
                        font-size: 22px;
                        text-align: left;
                        font-weight: normal;
                        margin-bottom: 32px;
                        margin-top: 11px;
                    }
                    p{
                        text-align: left;
                        margin-bottom: 17px;
                    }
                    ul{
                        padding-left: 17px;
                        li{
                            text-align: left;
                            margin-bottom: 16px;
                            list-style:disc;
                        }
                    }
                    .el-button{
                        width: 100%;
                        background: #1B1B1B;
                        border: none;
                        /*margin: 10px auto;*/
                        margin-top: 42px;



                    }
                    .el-button.is-round{
                        border-radius: 5px;
                    }
                }
            }
        }

    }
</style>